// за весь период
function hcAllPeriod(data)
{
	console.log(data);
	Highcharts.chart('js--hc-allPeriod', {

	    chart: {
	        type: 'column'
	    },
	    title: {
	        text: 'За весь период'
	    },
	    xAxis: {
	        type: 'category',
	    },
		yAxis: {
	        title: {
	            text: '',
	        },
	    },
	    legend: {
	        enabled: false
	    },
	    plotOptions: {
	        series: {
	            borderWidth: 0,
				dataLabels: {
	            	enabled: true,
	            	format: '{point.y:.1f}'
	            }
	        }
	    },
	    tooltip: {
	        headerFormat: '',
	        pointFormat: '<b>{point.y:.2f}</b>'
	    },
	    series: [{
	        colorByPoint: true,
	        data: JSON.parse(data)
	    }],

	});
}