/*
	Discounts functions
*/

// Create
$('body').on('click', '#js-discounts--create', function(e){
	e.preventDefault();

	var data = $('#js-discounts--form').serialize();

	$.ajax({
		url 	 : base_url + '/' + segment1 + '/' + segment2,
		type 	 : 'post',
		dataType : 'json',
		data 	 : data,

		beforeSend: function(){
	        LoaderStart();
	    },

	    complete: function(answer, xhr, settings){
	    	validationCreate(answer);
	    }
	});
});